.cover {
  .music {
    text-align: right;
    margin-top: -1.45rem;
    margin-right: -0.45rem;
    .music-button{
     background-color: transparent;
     background-image: none;
     border: solid 0.00rem;
     padding: 0.0rem 0rem;
    }
  }
  .english1 {
    color: #68575c;
    text-shadow: 2px 4px 3px #e0e0e0;
    margin-top: -2rem;
    margin-bottom: 1.5rem;
    line-height: 2rem;
    font-family: "Batang";
    letter-spacing: 0.15rem;
    font-size: 2rem;
  }
  .english2 {
    color: #68575c;
    text-shadow: 2px 4px 3px #e0e0e0;
    margin-top: -1.2rem;
    margin-bottom: 1.2rem;
    line-height: 2rem;
    font-family: "Batang";
    letter-spacing: 0.15rem;
    font-size: 2rem;
  }
  .english {
    color: #f7e0e8;
    text-shadow: 2px 4px 3px #ccc8c8;
    margin-top: -0.65rem;
    margin-bottom: -0.7rem;
    line-height: 2rem;
    word-spacing: 0.1rem;
    letter-spacing: 0.1rem;
    font-family: "Batang";
    height: 4rem;
    .wedding-text {
      color: #626262; 
      text-shadow: 2px 4px 3px #d1d0d1;
      font-size: 2.6rem;
      animation: colorChange 1s ease forwards;
      animation-delay: 2.5s;
    }
   
  }
  @keyframes colorChange {
  to {
    color: #bbb0d8; 
    text-shadow: 2px 4px 3px #dad9df; /* 원하는 변화가 있다면 같이 수정 */
  }
}
  @keyframes colorChange1 {
  to {
    color: #ccc7da;     
    text-shadow: 2px 4px 3px #dad9df; /* 원하는 변화가 있다면 같이 수정 */
  }
}
  .invitation-text {
  font-family: "Mapo";
  color: #969696; 
  margin-bottom: -0.3rem;
  font-size: 0.7rem;
  text-shadow: 2px 4px 3px #d1d0d1;
  animation: colorChange1 2.5s ease forwards;
  animation-delay: 2.5s;
  word-spacing: 0.3rem;
  letter-spacing: 0.45rem;
}
  .image-row {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 20px; /* 이미지와 이름 사이 간격 */
  margin-top: 5px;
}
  .groom-name {
  font-family: "Name";
  writing-mode: vertical-rl;
  text-orientation: upright;
  margin: 0 -1.7rem 0 0.6rem;
  letter-spacing: 1rem;
  font-size: 1.37rem;
}
  .bride-name {
  font-family: "Name";
  writing-mode: vertical-rl;
  text-orientation: upright;
  margin: 0 0.6rem 0 -1.7rem;
  letter-spacing: 1rem;
  font-size: 1.37rem;
}

  .image-wrapper {
    display: block; 
    margin: 0 auto;
    border-radius: 300px 300px 0 0;
    width: 72.7%;
    height: 27rem;
    overflow: hidden;
    display: flex;
    align-items: center;
    
    @keyframes fadeIn {
      0% {
        filter: grayscale(100%);
      }
      99% {
        filter: grayscale(100%);
      }
      100% {
        opacity: 1;
      }
    }
    img {
      animation: fadeIn 3.1s ease-in;
      width: 100%;
      height: auto;
    }
  }

  .subtitle {
    font-size: 1.8rem;
    font-family: "Allura";
    color: var(--theme-color);
    margin-top: -1rem;
    font-style: italic;
  }

  .names {
    font-size: 1.1rem;
    margin-top: 0.8rem;
    margin-bottom: 0.8rem;
    letter-spacing: 0.17rem;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 0.1rem;
    .divider {
      background-color: var(--dark-color);
      width: 1px;
      height: 0.7rem;
    }
  }
  .info {
    font-family: "Name";
    margin-top: -1.3rem;
    font-size: 1.1rem;
    margin-bottom: -1.7rem;
    color: #201f1f;
    opacity: 0.7;
    line-height: 2.2rem;
    word-spacing: 0.1rem;
  }
  .heart {
    margin-bottom: 0rem;
    line-height: 1.6rem;
    .hearticon1{
    fill: #cfcfcf;
    animation: colorChangeh1 1s ease forwards, shake 2.5s ease-in-out 2 ;
    animation-delay: 2.8s, 3s;
    }
    .hearticon2{
    fill: #9b9b9b;
    animation: colorChangeh2 1s ease forwards, shake 2.5s ease-in-out 2 ;
    animation-delay: 2.8s, 3s;
    }
    .hearticon3{
    fill: #666666;
    animation: colorChangeh3 1s ease forwards, shake 2.5s ease-in-out 2 ;
    animation-delay: 2.8s, 3s;
    }
  }
@keyframes shake {
  0%   { transform: translateY(0); }
  25%  { transform: translateY(-12px); }
  50%  { transform: translateY(0); }
  75%  { transform: translateY(-10px); }
  100% { transform: translateY(0); }
}
@keyframes colorChangeh1 {
  to {
    fill: #e9e7f0;
  }
}
@keyframes colorChangeh2 {
  to {
    fill: #d9d4e7;
  }
}
@keyframes colorChangeh3 {
  to {
    fill: #ccc3e7;
  }
}
}
