.information {
  .info-card {
    margin-bottom: 0rem;
    padding: 2rem 0rem 1rem 0rem;
    position: relative;
    border-radius: 0.5rem;
    .label {
      position: relative;
      left: 50%;
      top: -1rem;
      transform: translateX(-50%);
      padding: 0.5rem 0.5rem 0.5rem 0.5rem;
      color: rgb(66, 66, 66);
      background-color: var(--alt-bg-color);
      border-radius: 1rem;
    }
  }
  .button.groom-button{
    border-radius: 0.5rem;
    border: 1.5px solid;
    color: rgb(85, 107, 170);
    border-color: rgb(189, 202, 236);
  }
  .button.bride-button{
    border-radius: 0.5rem;
    border: 1.5px solid;
    color: rgb(189, 105, 146);
    border-color: rgb(236, 193, 212);
  }
  .content {
    line-height: 1.9;
    font-size: 0.85rem;
    margin-top: -1.5rem;
    margin-bottom: -0.5rem;
  }
  .content-bottom {
    line-height: 1.9;
    font-size: 0.85rem;
    margin-top: -1.2rem;
    margin-bottom: -0.7rem;
  }
}

.donation-modal {
  width: 22rem;
  @media (max-width: 500px) {
    width: 92%;
  }
  .header {
    .groom-title {
      text-align: center;
      font-size: 1.2rem;
      color: rgb(85, 107, 170);
      padding-bottom: 1rem;
      border-bottom: 1px solid var(--dark-grey-color);
    }
    .bride-title {
      text-align: center;
      font-size: 1.2rem;
      color: rgb(189, 105, 146);
      padding-bottom: 1rem;
      border-bottom: 1px solid var(--dark-grey-color);
    }
  }
  .content {
    padding: 0 0.7rem 0 0.7rem;

    .account-info {
      margin-top: 1rem;
      margin-bottom: 1.5rem;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
      .name {
        margin-bottom: 0.3rem;
        .relation {
          opacity: 0.7;
        }
      }
      .groom-copy-button {
        font-size: 0.8rem;
        padding: 0.4rem 0.8rem;
        border: 1.5px solid;
        color: rgba(85, 108, 170, 0.856);
        border-color: rgb(166, 177, 206);
      }
      .bride-copy-button {
        font-size: 0.8rem;
        padding: 0.4rem 0.8rem;
        border: 1.5px solid;
        color: rgb(189, 105, 146, 0.856);
        border-color: rgb(199, 169, 183);
      }
    }
  }

  .footer {
    display: flex;
    flex-direction: column;
  }
}

.attendance-modal {
  width: 22rem;

  .header {
    .title {
      text-align: center;
      font-size: 1.2rem;
      color: var(--theme-color);
      padding-bottom: 1rem;
      border-bottom: 1px solid var(--dark-grey-color);
    }
  }

  .content {
    padding: 0 1rem 1rem 1rem;
    .form {
      display: flex;
      flex-direction: column;
      margin-top: 1rem;
      gap: 1.5rem;

      .input-group {
        display: flex;
        flex-direction: column;
        gap: 0.2rem;

        .input {
          display: flex;
          flex-direction: column;
        }
        .select-input {
          display: flex;
          flex-direction: row;

          label {
            width: 100%;
            span {
              padding: 0.4rem;
              display: inline-block;
              text-align: center;
              width: 100%;
              background-color: var(--light-grey-color);
            }
            input {
              &:checked + span {
                border: 2px solid var(--alt-color);
              }
              & + span {
                border: 2px solid var(--light-grey-color);
              }
            }
          }
        }

        .radio-input {
          display: flex;
          flex-direction: row;
          justify-content: left;
          gap: 2rem;
        }

        input {
          padding: 0.5rem;
          margin-bottom: 0.5rem;
          border: 1px solid var(--light-grey-color);
          border-radius: 0.3rem;
          font-size: 0.8rem;
        }
      }
    }
  }

  .footer {
    display: flex;
    flex-direction: row;
    button {
      width: 100%;
    }
  }
}

.attendance-info-modal {
  width: 22rem;

  .header {
    .title {
      text-align: center;
      font-size: 1.2rem;
      color: var(--theme-color);
      padding-bottom: 1rem;
      border-bottom: 1px solid var(--dark-grey-color);
    }
  }

  .content {
    padding: 1.5rem 1rem 1.5rem 1rem;
    .info-message {
      text-align: center;
      line-height: 1.5rem;
      padding-bottom: 1.5rem;
      border-bottom: 1px dotted var(--dark-grey-color);
    }
    .wedding-info {
      padding-top: 1.5rem;
      padding-left: 2rem;
      line-height: 1.5rem;
      svg {
        width: 0.8rem;
        height: 0.8rem;
      }
    }
  }

  .footer {
    display: flex;
    flex-direction: row;
    button {
      width: 100%;
    }
  }
}
