.guestbook,
.all-guestbook-modal {
  .english{
    margin-bottom: 0.35rem;
  }
  .thanksto{
    background-color: transparent;
    // background-image: url("https://www.transparenttextures.com/patterns/hexellence.png");
    margin-top: 1rem;
    margin-bottom: -0.5rem;
    padding-top: 0.5rem;
    padding-bottom: 0.9rem;
    line-height: 1.2rem;
    font-size: 0.7rem;
    .flower{
      margin-top: -1.7rem;
      margin-bottom: -5.7rem;
      z-index: 1;
    }
    .content{
      position: relative;
      z-index: 2;
    }
  }
  .post {
    text-align: left;
    box-shadow: 0.1rem 0.1rem 0.3rem 0 rgba(0, 0, 0, 0.1);
    background-color: var(--white-color);
    border-radius: 0.5rem;
    border: 1px solid var(--light-grey-color);
    font-size: 0.9rem;
    margin-top: 0.2rem;

    .heading {
      margin: 0.5rem;
      display: flex;
      justify-content: flex-end;
      .close-button {
        position: relative;
        z-index: 3;
        width: 0.8rem;
        height: 0.8rem;
        &::before {
          content: "";
          width: 0.1rem;
          height: 100%;
          background-color: var(--dark-grey-color);
          position: absolute;
          transform: rotate(45deg);
          top: 0;
          left: 50%;
        }
        &::after {
          content: "";
          width: 0.1rem;
          height: 100%;
          background-color: var(--dark-grey-color);
          position: absolute;
          transform: rotate(135deg);
          top: 0;
          left: 50%;
        }
      }
    }

    .body {
      margin: 0 1.3rem 1.3rem 1.3rem;
      .title {
        display: flex;
        align-items: center;
        .name {
          margin-right: 1rem;
          margin-top: -0.6rem;
        }
        .date {
          margin-top: -0.6rem;
          font-size: 0.8rem;
          opacity: 0.5;
        }
        margin-bottom: 0.8rem;
      }
      .content {
        word-wrap: break-word;
        white-space: pre-wrap;
      }
    }
  }
}

.write-guestbook-modal {
  width: 24rem;
  @media (max-width: 500px) {
    width: 95%;
  }
  .header {
    .title-group {
      text-align: center;
      .title {
        font-size: 1.2rem;
        color: var(--theme-bg-color);
        padding: 0.5rem;
      }
      .subtitle {
        font-size: 0.9rem;
        color: var(--theme-color);
      }
      padding-bottom: 1rem;
      border-bottom: 1px solid var(--dark-grey-color);
    }
  }

  .content {
    padding: 0 0.7rem 0.7rem 0.7rem;

    .form {
      display: flex;
      flex-direction: column;
      margin-top: 0.5rem;

      input {
        padding: 0.5rem;
        margin-bottom: 0.5rem;
        border: 1px solid var(--light-grey-color);
        border-radius: 0.3rem;
        font-family: "Name";
        font-size: 0.8rem;
      }

      textarea {
        padding: 0.5rem;
        margin-bottom: 0.5rem;
        border: 1px solid var(--light-grey-color);
        border-radius: 0.3rem;
        font-family: "Name";
        font-size: 0.8rem;
        height: 5rem;
      }
    }
  }

  .footer {
    display: flex;
    flex-direction: row;
    button {
      width: 100%;
    }
  }
}

.all-guestbook-modal {
  width: 24rem;
  @media (max-width: 500px) {
    width: 95%;
  }
  .header {
    > .title {
      text-align: center;
      font-size: 1.2rem;
      color: var(--theme-bg-color);
      padding-bottom: 1rem;
    }
  }

  > .content {
    padding: 0 1rem 0.7rem 1rem;

    .pagination {
      display: flex;
      justify-content: center;
      margin-top: 0.5rem;
      margin-bottom: 0.5rem;
      .page {
        padding: 0.3rem 0.5rem;
        margin: 0 0.2rem;
        border: 1px solid var(--dark-grey-color);
        border-radius: 0.3rem;
        &.current {
          background-color: var(--alt-color);
          color: var(--white-color);
        }
      }
    }
  }

  .footer {
    display: flex;
    flex-direction: column;
  }
}

.delete-guestbook-modal {
  width: 22rem;

  .header {
    .title {
      text-align: center;
      font-size: 1.2rem;
      color: var(--theme-color);
    }
  }

  .content {
    padding: 0 0.7rem 0.7rem 0.7rem;

    .form {
      display: flex;
      flex-direction: column;
      margin-top: 1rem;

      input {
        padding: 0.5rem;
        margin-bottom: 0.5rem;
        border: 1px solid var(--light-grey-color);
        border-radius: 0.3rem;
        font-family: "MapoGoldenPier";
        font-size: 0.8rem;
      }
    }
  }

  .footer {
    display: flex;
    flex-direction: row;
    button {
      width: 100%;
    }
  }
}
