@use "./component/cover";
@use "./component/invitation";
@use "./component/button";
@use "./component/bgEffect";
@use "./component/calendar";
@use "./component/gallery";
@use "./component/location";
@use "./component/information";
@use "./component/guestbook";
@use "./component/store";
@use "./component/shareButton";

:root {
  --bg-color: rgba(251, 250, 255, 0.959);
  --theme-color: #aea6ca;
  --theme-bg-color: rgb(132, 110, 167);
  --heart-bg-color: #e3ddf8;
  --alt-color: rgb(207, 197, 230);
  --alt-bg-color: #ece8f7;
  --dark-color: #1a1718;
  --dark-grey-color: #d2d2d2;
  --light-grey-color: #ececec;
  --light-color: #fefdff9c;
  --white-color: #ffffff;
  --red-color: #f01d1d;

  font-family: "Name";
  font-size: 1.25rem;
  -webkit-text-size-adjust: none;
  text-size-adjust: none;

  @media (min-width: 375px) and (max-width: 500px) {
    font-size: 4vw;
  }
  
   @media (max-width: 375px) {
    font-size: 3.3vw;
  }

}

@media print {
  body {
    print-color-adjust: exact;
  }
}

$colors: (
  theme-color: var(--theme-color),
  theme-bg-color: var(--theme-bg-color),
  alt-color: var(--alt-color),
  alt-bg-color: var(--alt-bg-color),
  dark-color: var(--dark-color),
  dark-grey-color: var(--dark-grey-color),
  light-grey-color: var(--light-grey-color),
  light-color: var(--light-color),
  white-color: var(--white-color),
  red-color: var(--red-color),
);

@each $name, $color in $colors {
  .text-#{$name} {
    color: $color !important;
  }
  .bg-#{$name} {
    background-color: $color !important;
  }
  .border-#{$name} {
    border-color: $color !important;
  }
}

@keyframes lazy-fade-in {
  from {
    opacity: 0;
    z-index: 2;
    transform: translateY(2rem);
  }
  to {
    opacity: 1;
    z-index: 2;
    transform: translateY(0);
  }
}

button {
  font-family: "Name";
  font-size: 1rem;
  border: none;
  background-color: transparent;
  padding: 0;
}

body {
  margin: 0;
  overflow-x: hidden;
  overflow-y: auto;
  height: 100vh;
  &.modal-open {
    overflow-y: hidden;
  }

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

* {
  box-sizing: border-box;
  color: var(--dark-color);
  user-select: none;
}

.background {
  background-color: var(--bg-color);
  background-image: url("https://www.transparenttextures.com/patterns/hexellence.png");
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  min-width: 100vw;

  .card-view {
    width: 980px;
    text-align: center;
    @media (max-width: 980px) {
      width: 500px;
    }

    @media (max-width: 500px) {
      width: 100vw;
    }

    .card-group {
      position: relative;
      @media (width > 980px) {
        display: flex;
        flex-direction: row;
        box-shadow: 0 0 1px 0 rgba(255, 255, 255, 0);
        margin: 0px;
        opacity: 0;
        &.lazy-active {
          animation-name: lazy-fade-in;
          animation-duration: 2s;
          opacity: 1;
        }

        @media print {
          opacity: 1;
          animation-name: inherit !important;
          animation-duration: inherit !important;
        }
      }

      .card {
        background-color: var(--light-color);
        padding: 1.5rem 0.5rem 0.5rem 0.5rem;
        border: 0.5px solid var(--light-grey-color);
        width: 50%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        position: relative;

        @media (max-width: 980px) {
          margin: 0rem;
          width: inherit;
          Border-color :rgba(255, 255, 255, 0);
          opacity: 0;
          &.lazy-active {
            animation-name: lazy-fade-in;
            animation-duration: 2s;
            opacity: 1;
          }

          @media print {
            opacity: 1;
            animation-name: inherit !important;
            animation-duration: inherit !important;
          }
        }
      }
    }

    .footer {
      position: relative;
      background-color: var(--light-color);
      box-shadow: 0 0 0.5rem 0 rgba(0, 0, 0, 0);
      margin: 0rem;
      opacity: 0;
      padding: 1rem;
      opacity: 0;
      &.lazy-active {
        animation-name: lazy-fade-in;
        animation-duration: 0.1s;
        opacity: 1;
      }

      @media print {
        opacity: 1;
        animation-name: inherit !important;
        animation-duration: inherit !important;
      }
    }
  }
}

h1 {
  font-size: 3em;
  color: var(--dark-color);
  font-weight: bold;
  &.english {
    font-family: "Name";
  }
  margin-bottom: -1.5rem;
}

h2 {
  font-size: 3.1rem;
  color: var(--theme-color);
  font-weight: normal;
  &.english {
    font-family: "Allura";
    margin-top: 1.5rem;
    margin-bottom: 0.7rem;
  }
  font-style: italic;
}
mark {
    background-color: #ece8f7;
    color: #000000;
    padding: 2.5px 5px;
    border-radius: 3px;
    font-size: 1.05rem;
    word-spacing: 0.15rem;
  }
div.break {
  height: 1rem;
}
